import React, { useState, useEffect } from 'react';
import { withRouter } from 'react-router-dom';
import {
  Form,
  Tabs,
  Table,
  Button,
  Modal,
  Row,
  Col,
  message,
  Input,
  DatePicker,
  Select,
  Tooltip,
  Popconfirm,
} from 'antd';

import { injectIntl } from 'react-intl';
import {
  getProjects,
  getActors,
  getProcessTypes,
  addProject,
  getClients,
  deleteProject,
  saveProject,
} from '../../services';
import moment from 'moment';

const { TabPane } = Tabs;
const FormItem = Form.Item;
const { Option } = Select;

const ProjectsCmp = props => {
  const {
    user,
    form,
    form: { getFieldDecorator },
    intl: { messages },
  } = props;

  const [visible, setVisible] = useState(false);
  const [loading, setLoading] = useState(true);
  const [projects, setProjects] = useState([]);
  const [project, setProject] = useState([]);
  const [actors, setActors] = useState([]);
  const [process, setProcess] = useState([]);
  const [clients, setClients] = useState([]);
  const [mode, setMode] = useState('add');

  useEffect(() => {
    fetch();
    fetchProcess();
    if (user && user.rol === 'admin') {
      fetchActors();
      fetchClients();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const fetch = async () => {
    try {
      setLoading(true);
      const { data } = await getProjects();
      setProjects(data.projects);
      setLoading(false);
    } catch (error) {
      setLoading(false);
      message.error(error.message);
    }
  };

  const fetchActors = async () => {
    try {
      const { data } = await getActors();
      setActors(data.actors);
    } catch (error) {
      message.error(error.message);
    }
  };

  const fetchProcess = async () => {
    try {
      const { data } = await getProcessTypes();
      setProcess(data);
    } catch (error) {
      message.error(error.message);
    }
  };

  const fetchClients = async () => {
    try {
      const { data } = await getClients();
      setClients(data.clients);
    } catch (error) {
      message.error(error.message);
    }
  };

  const remove = async record => {
    try {
      setLoading(true);
      await deleteProject(record.id);
      fetch();
    } catch (error) {
      setLoading(false);
      message.error(error.message);
    }
  };

  const handleReset = () => {
    form.resetFields();
  };

  const handleSubmit = e => {
    e.preventDefault();

    form.validateFields(async (err, values) => {
      if (!err) {
        setLoading(true);
        try {
          if (mode === 'edit') {
            const form = {
              name: values.name,
              PMId: values.PMId || null,
              status: values.status || null,
            };
            await saveProject(project.id, form);
          } else {
            const form = {
              name: values.name,
              dueDate: values.dueDate,
              projectProcess: values.projectProcess,
              PMId: values.PMId || null,
              clientId: values.PMId || null,
              status: values.status || null,
            };
            await addProject(form);
          }

          message.success(messages.default.success);
          handleReset();
          setVisible(false);
          fetch();
        } catch (error) {
          setVisible(true);
          setLoading(false);
          message.error(error.message);
        }
      }
    });
  };

  const showModal = value => {
    handleReset();
    setMode(value);
    setVisible(true);
  };

  const columns = [
    {
      title: 'Id',
      dataIndex: 'id',
      key: 'id',
    },
    {
      title: 'Name',
      dataIndex: 'name',
      key: 'name',
    },
    {
      title: 'ClientId',
      dataIndex: 'UserId',
      key: 'UserId',
    },
    {
      title: 'Status',
      dataIndex: 'status',
      key: 'status',
      render: value => {
        if (value === '0') return 'Inactive';
        if (value === '1') return 'Active';
        if (value === '2') return 'Pause';
        if (value === '3') return 'Finished';
      },
    },
    {
      title: 'dueDate',
      dataIndex: 'dueDate',
      key: 'dueDate',
    },
    {
      title: 'startDate',
      dataIndex: 'startDate',
      key: 'startDate',
    },
    {
      title: 'endDate',
      dataIndex: 'endDate',
      key: 'endDate',
    },
    {
      title: 'Process',
      dataIndex: 'ProcessType',
      key: 'ProcessType',
      render: value => {
        return <div>{value.variableName}</div>;
      },
    },
    {
      title: 'PMId',
      dataIndex: 'Actor',
      key: 'Actor',
      render: value => {
        if (value) {
          return (
            <div>
              {value.name} (<small>{value.email}</small>)
            </div>
          );
        } else {
          return <div>---</div>;
        }
      },
    },
    {
      title: '',
      key: 'actions',
      width: 100,
      render: (value, record) => {
        return (
          <React.Fragment>
            <Tooltip placement="top" title="Edit Actor">
              <Button
                style={{ marginRight: 5 }}
                onClick={() => {
                  setProject(record);
                  showModal('edit');
                }}
                size="small"
                type="primary"
                icon="edit"
              />
            </Tooltip>
            <Popconfirm
              onConfirm={() => {
                remove(record);
              }}
              title="Are you sure?"
              okText={messages.default.yes}
              cancelText={messages.default.no}
            >
              <Tooltip placement="top" title="Delete Project">
                <Button size="small" type="danger" icon="delete" />
              </Tooltip>
            </Popconfirm>
          </React.Fragment>
        );
      },
    },
  ];

  const operations = (
    <React.Fragment>
      <Button
        loading={loading}
        onClick={() => {
          showModal('add');
        }}
        type="primary"
        icon="smile"
      >
        Add
      </Button>
    </React.Fragment>
  );

  const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 8 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 16 },
    },
  };

  const config = {
    initialValue: project && project.dueDate ? moment(project.dueDate).startOf('days') : '',
    rules: [{ type: 'object', required: true, message: 'Please select time!' }],
  };

  const getProjectProcess = () => {
    if (project && project.ProcessTypeId) {
      const aux = process.filter(item => item.id === project.ProcessTypeId);
      if (aux.length) {
        return aux[0].variableName;
      }
      return '';
    } else {
      return '';
    }
  };

  return (
    <div>
      <div style={{ padding: 24, background: '#fff', minHeight: 360 }}>
        <Tabs defaultActiveKey="all" tabBarExtraContent={operations}>
          <TabPane tab="Projects" key="all">
            <Table
              size="small"
              loading={loading}
              columns={columns}
              dataSource={projects}
              rowKey={record => record.id}
            />
          </TabPane>
        </Tabs>

        <Modal
          centered
          width={600}
          title="Add Actor"
          visible={visible}
          onCancel={() => {
            setVisible(false);
          }}
          footer={[
            <Button
              key="back"
              onClick={() => {
                setVisible(false);
              }}
            >
              {messages.default.cancel}
            </Button>,
            <Button key="submit" type="primary" loading={loading} onClick={handleSubmit}>
              {messages.default.create}
            </Button>,
          ]}
        >
          <Form {...formItemLayout}>
            <Row justify="center" type="flex">
              <Col xs={24}>
                <FormItem label="Name" hasFeedback>
                  {getFieldDecorator('name', {
                    initialValue: project && project.name ? project.name : '',
                    rules: [
                      {
                        required: true,
                        // message: messages.eds.rules.node_id.required,
                      },
                    ],
                  })(<Input />)}
                </FormItem>
                <FormItem label="Due Date">
                  {getFieldDecorator('dueDate', config)(<DatePicker disabled={mode === 'edit'} />)}
                </FormItem>
                <FormItem label="Process" hasFeedback>
                  {getFieldDecorator('projectProcess', {
                    initialValue: getProjectProcess(),
                    rules: [
                      {
                        required: true,
                        // message: messages.eds.rules.model_id.required,
                      },
                    ],
                  })(
                    <Select disabled={mode === 'edit'}>
                      {process.map(item => (
                        <Option value={item.variableName}>{item.name}</Option>
                      ))}
                    </Select>
                  )}
                </FormItem>

                <FormItem label="Status" hasFeedback>
                  {getFieldDecorator('status', {
                    initialValue: project && project.status ? project.status : '',
                    rules: [
                      {
                        required: true,
                        // message: messages.eds.rules.model_id.required,
                      },
                    ],
                  })(
                    <Select>
                      <Option value="0">Inactive</Option>
                      <Option value="1">Active</Option>
                      {user && user.rol && <Option value="2">Pause</Option>}
                      {user && user.rol && <Option value="3">Finished</Option>}
                    </Select>
                  )}
                </FormItem>

                {user && user.rol === 'admin' && (
                  <React.Fragment>
                    <FormItem label="Client" hasFeedback>
                      {getFieldDecorator('clientId', {
                        rules: [
                          {
                            required: true,
                            // message: messages.eds.rules.model_id.required,
                          },
                        ],
                      })(
                        <Select disabled={mode === 'edit'}>
                          {clients.length > 0 &&
                            clients.map(item => (
                              <Option value={item.id}>
                                {item.fullName} (<small>{item.email}</small>)
                              </Option>
                            ))}
                        </Select>
                      )}
                    </FormItem>
                    <FormItem label="Actor" hasFeedback>
                      {getFieldDecorator('PMId', {
                        rules: [
                          {
                            required: true,
                            // message: messages.eds.rules.model_id.required,
                          },
                        ],
                      })(
                        <Select>
                          {actors.map(item => (
                            <Option value={item.id}>{item.name}</Option>
                          ))}
                        </Select>
                      )}
                    </FormItem>
                  </React.Fragment>
                )}
              </Col>
            </Row>
          </Form>
        </Modal>
      </div>
    </div>
  );
};
const WrappedProjectsCmp = Form.create()(injectIntl(ProjectsCmp));

export default withRouter(WrappedProjectsCmp);
